import http from "xt/http";
import baseUrl from '../config'

//
const getSystemDicChildren = (parentId) => (http.get(baseUrl  + '/web/system/dic/getSystemDicChildrenByParentId', { parentId }));

//
const getSystemDicByParent = () => (http.get(baseUrl  + '/web/system/dic/getSystemDicByParentIdContent', { parentId: 'PH', pageSize: '100' }));

// 
const getFaceRole = (data) => {
	return new Promise((resolve, reject) => {
		http.get('/subsystem/face/app/parking/getFaceRoleTime', data)
			.then(res => {
				resolve(res)
			}).catch(err => {
				reject(err)
			})
	})
};

