import http from "../http";

//
const getAllNoteService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-discovery-forum/note/getAppNoteList', reqData, 'homeUrl', false).then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        });
    });
}

//
const saveLikeService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-discovery-forum/thumpUp/thumpUp', reqData).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        });
    });
}

