const addNoteService = (content, userCode, areaCode, picUrl) => {
    return new Promise((resolve, reject) => {
        http.post('xwj-discovery-forum/note/sendNote', {
            "areaCodeList": [areaCode],
            "content": content,
            "isProperty": 1,
            "picUrl": picUrl,
            "userCode": userCode
        }).then(result => {
            result.resultCode == '0' ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        });
    });
}


